html {
  font-size: 10px;
}

* {
  box-sizing: border-box;
}

:root {
  --color-veraneos: #14716a;
  --color-superdarkaneos: #013839;
  --color-darkaneos: #005c56;
  --color-lime: #12b886;
  --color-lime-dark: #038b62;
  --color-lime-darker: #095d44;
  --color-lime-lightest: #d6f8ee;
  --color-blue: #129cab;
  --color-ink: #343a40;
  --color-ink-light: #868e96;
  --color-ink-lighter: #adb5bd;
  --color-paper-darker: #eee9e5;
  --color-paper-dark: #f5f3f1;
  --color-paper: #faf9f8;
  --color-paper-light: #fdfcfb;
  --color-white: #fff;
  --color-red: #db2c24;
  --color-red-light: #eb706a;
  --color-gold: #ffb200;
  --color-gold-lightest: #fff3d2;
  --color-orange: #ee641c;
  --color-orange-light: #f79a57;
  --color-orange-lighter: #ffc194;
  --color-pink: #faab95;
  --color-green-800: #003526;

  /* Breakpoints */
  --breakpoint-sm: 768px;
  --breakpoint-md: 1024px;
  --breakpoint-lg: 1280px;

  /* Constants */
  --header-height: 60px;
}

p {
  margin: 0;
}

.itinerary-container {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  padding: 2em;

  @media (min-width: 1280px) {
    padding: 2em 20%;
  }
}

/* Animations */
.animated-bg {
  background: radial-gradient(
      115.76% 129.32% at 6.39% 9.17%,
      #00543c 0%,
      rgba(255, 255, 255, 0) 100%,
      rgba(255, 255, 255, 0) 100%
    ),
    radial-gradient(
      97.94% 127.95% at 100% 3.89%,
      #0c6d2b 48.53%,
      rgba(176, 241, 252, 0) 100%
    ),
    radial-gradient(
      276.13% 360.74% at -103.89% 222.22%,
      #b0f1fc 48.24%,
      rgba(255, 236, 179, 0) 80.71%
    );
  animation: colorChange 5s infinite alternate linear;
}

@keyframes colorChange {
  from {
    /* radial-gradient(farthest-corner at top right, ..) */
    background-position: left top;
    background-size: 150% 150%;
  }
  49.9% {
    background-position: left top;
  }
  50% {
    /* radial-gradient(farthest-corner at top center, ..) */
    background-size: 100% 100%;
  }
  50.1% {
    background-position: right top;
  }
  to {
    /* radial-gradient(farthest-corner at top left, ..) */
    background-position: right top;
    background-size: 150% 150%;
  }
}
